import { createRouter, createWebHistory } from 'vue-router';
import Home from '../views/Home';
import Search from '../views/Search';

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/search',
        name: 'Search',
        component: Search
    }
]
const router = createRouter({
    history: createWebHistory('/favorite/'),
    routes
});

export default router;