<template>
    <div class="search-box">
        <div class="search-bar">
            <input @focus="toggleFocus" @blur="toggleFocus" v-model="searchPhrase" @input="searchSongs" type="text" class="search-bar">
            <img src="../../assets/magn_glass.svg" alt="magnifying glass" class="icon">
        </div>
        <auto-suggestion @popup="handlePopUp" v-show="focus" :songs="songs"/>
    </div>
</template>

<script>
import AutoSuggestion from './AutoSuggestion.vue';

export default {
  components: { AutoSuggestion },
    name: "search-bar",
    data() {
        return { 
            focus: false,
            searchPhrase: "",
            songs: []
        }
    },
    methods: {
        handlePopUp(song) {
            this.$emit('popup', song);
        },
        toggleFocus() {
            this.focus = !this.focus;
        },
        async searchSongs() {
            this.songs = await fetch(
                `https://api.spotify.com/v1/search?q=${this.searchPhrase}&type=track%2Cartist&market=US&limit=10&offset=5`, {
                headers: {
                    'Authorization': `Bearer ${process.env.VUE_APP_TOKEN}`
                }
            })
            .then(data => data.json())
            .catch(err => {return err;});

            try {
                this.songs = this.songs.tracks.items;
            } catch {
                this.songs = [];
            }
        }
    }
}


</script>

<style scoped>
    .search-box {
        width: 90vw;
        max-width: 600px;
        margin: auto;
        position: relative;
        z-index: 1;
    }

    .search-bar {
        height: 60px;
        box-sizing: border-box;
        color: #FFF;
        font-size: 26px;
    }
    
    .search-bar input {
        padding: .2em 1em;
        border-radius: 10% / 50%;
        border: 1px solid #FFF;
        width: 100%;
        outline-style: none;
        background-color: rgba(33, 37, 41);
    }

    .search-bar input:focus {
        border-width: 2px;
    }

    @media (min-width: 250px) {
        .search-bar input {
            border-radius:9% / 50%;
        }
    }

    @media (min-width: 600px) {
        .search-bar input {
            border-radius:5% / 50%;
        }
    }

    .icon {
        position: absolute;
        height: 30px;
        right: 30px;
        top: 15px;
    }
</style>