<template>
  <pop-up @close="this.$emit('close')">
      <div class="song-info">
        <p class="popup-title">Song Details:</p>
            <div class="container">
                <div class="row">
                    <div class="col-xs-12 col-md-4 main-info">
                        <div>
                            <img class="song-info__album-cover" :src="albumCover" alt="">
                        </div>
                        <div>
                            <p class="song-info__artist">{{ song.artists[0].name }}</p>
                            <p class="song-info__title">{{ songTitle }}</p>
                        </div>
                    </div>
                    <div class="col-md-8 additional-info">
                        <div class="container p-0">
                            <div class="column">
                                <div class="d-flex justify-content-around w-100">
                                    <div class="song-info__detail">
                                        <p class="song-info__detail__label">Album:</p>
                                        <p class="song-info__detail__content">{{ song.album.name }}</p>
                                    </div>
                                    <div class="song-info__detail">
                                        <p class="song-info__detail__label">Release Date:</p>
                                        <p class="song-info__detail__content">{{ song.album.release_date }}</p>
                                    </div>
                                </div>
                                <div class="song-info__detail my-4 pt-2">
                                    <div class="song-info__detail__label">Song Preview:</div>
                                    <audio ref="audio-player" controls>
                                        <source :src="song.preview_url" type="audio/mpeg" />
                                    </audio>
                                </div>
                                <div class="d-flex justify-content-end">
                                    <button @click="addFavorite" class="btn btn-success">Add Favorite</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
      </div>
  </pop-up>
</template>

<script>
import PopUp from'../PopUp.vue';
import noPreviewImage from '../../assets/no-preview.png';

export default {
    name: 'song-info',
    components: {
        PopUp
    },
    props: {
        song: Object,
    },
    methods: {
        addFavorite() {
            let body = new URLSearchParams();
            body.append("id", this.song.id);
            body.append("cover_url", this.albumCover);
            body.append("artist", this.song.artists[0].name);
            body.append("title", this.song.name);
            
            fetch(`${process.env.VUE_APP_API_URL}/songs/create`, {
                method: 'POST',
                body
            });
        }
    },
    computed: {
        songTitle() {
            let title = this.song.name;
            let lengthThreshold = 75;
            return title.length > lengthThreshold 
            ? title.slice(0, lengthThreshold) + "..."
            : title;
        },
        albumCover() {
            if(this.song.album.images[1].url)
                return this.song.album.images[1].url;
            else
                return noPreviewImage;
        },
    },
    mounted() {
        let audio = this.$refs["audio-player"];
        audio.volume = 0.2;
    }
}
</script>

<style scoped>

    .popup-title {
        font-size: 24px;
        font-weight: 500;
        padding-bottom: 15px;
        text-align: center;
    }

    .popup-title::after {
        content: '';
        width: 130px;
        height: 2px;
        margin: 2px auto;
        background: rgb(156, 156, 156);
        display: block;
    }

    .song-info {
        color: rgb(224, 224, 224);
        text-align: left;
        margin-left: 10px;
        width: 100%;
    }

    .song-info__album-cover {
        width: 145px;
    }
    
    .song-info__artist {
        text-align: center;
        width: 160px;
        font-size: 15px;
        font-weight: bold;
        padding-top: 15px;
    }

    .song-info__title {
        width: 160px;
        font-size: 12px;
        text-align: center;
        padding-top: 3px;
    }

    .song-info__label {
        font-weight: 600;
    }


    .song-info__detail {
        text-align: center;
    }

    .song-info__detail__label {
        font-weight: 600;
        max-width: 150px;
    }

    .song-info__detail__content {
        font-size: 12px;
        max-width: 150px;
    }
    
    audio {
        margin-top: 10px;
        border-radius: 5px;
        height: 35px;
        background-color: #FFF;
    }

    .main-info {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: 25px 0;
    }

    @media screen and (min-width: 768px) {
        .main-info {
            flex-direction: column;
        }

        .popup-title {
            width: 160px;
        }
    }
</style>