<template>
    <div class="d-flex justify-content-center align-items-center">
        <div class="popup">
            <div @click="this.$emit('close')" class="popup__close">
                <div class="cross"></div>
            </div>
            <slot />
        </div>
        <div class="popup__overlay"></div>
    </div>
</template>

<script>
export default {
    name: 'pop-up',
    props: {
        song: Object,
    },
}
</script>

<style>

    .popup {
        position: absolute;
        z-index: 99996;
        min-height: 350px;
        max-width: 450px;
        background-color: rgb(33, 37, 41);
        border-radius: 7px;
        display: flex;
        padding: 25px;
    }

    .popup__overlay {
        width: 100vw;
        height: 100vh;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 99994;
        background-color: rgba(0, 0, 0, 0.5);
    }

    .popup__close {
        width: 11px;
        height: 11px;
        position: absolute;
        top: 10px;
        right: 10px;
    }

    .popup__close:hover {
        cursor: pointer;
    }

    .cross {
        transform: rotate(45deg) translate3d(3px, -2px, 0);
    }

    .cross::after {
        content: '';
        transform: rotate(90deg);
    }

    .cross, .cross::after {
        display: block;
        width: 2px;
        height: 11px;
        background-color: #FFF;
    }

    @media screen and (min-width: 768px) {
        .popup {
            min-width: 550px;
            width: auto;
        }
    }
</style>