<template>
  <div>
    <p class="title">
      My favorite songs
    </p>
    <div class="container mt-5">
      <div class="row">
          <song-card class="align-self-center" v-for="song in favoriteSongs" :key="song.id" :song="song"/>

      </div>
    </div>
  </div>
</template>

<script>
import SongCard from '../components/favorite/SongCard.vue';
export default {
  name: 'Home',
  components: { SongCard },
  data() {
    return {
      favoriteSongs: []
    }
  },
  async mounted() {
    let song = await fetch(`${process.env.VUE_APP_API_URL}/songs/`)
    .then(data => data.json());

    this.favoriteSongs = song;
  }
}
</script>

