<template>
  <main>
    <div class="content">
      <p class="title">
        Search for your favorite song:
      </p>
      <search-bar @popup="showPopUp" />
    </div>
    <song-info v-if="isPopUpVisible" @close="closePopUp" :song="song" />
  </main>
</template>

<script>
import SearchBar from '../components/search/SearchBar.vue';
import SongInfo from '../components/search/SongInfo.vue';

export default {
  components: { SearchBar, SongInfo },
  name: 'Search',
  data() {
    return {
      isPopUpVisible: false,
      song: {}
    }
  },
  methods: {
    showPopUp(song) {
      this.song = song;
      this.isPopUpVisible = true;
    },
    closePopUp() {
      this.isPopUpVisible = false;
    }
  }
}
</script>

<style scoped>

  .title {
    font-size: 48px;
    color: #FFF;
    transform: translateY(-30px);
  }

</style>
