<template>
  <div class="auto-suggestion">
      <template v-if="songs.length !== 0">
        <suggestion @popup="this.$emit('popup', song)" v-for="song in songs" :song="song" :key="song.id"/>
      </template>
      <template v-else>
          <p class="text-secondary mt-4 text-left">No songs found...</p>
      </template>
  </div>
</template>

<script>
import Suggestion from '../search/Suggestion.vue';

export default {
    name: 'auto-suggestion',
    components: {
        Suggestion
    },
    props: {
        songs: Array
    }

}
</script>

<style scoped>
    .auto-suggestion {
        width: 100%;
        min-height: 150px;
        background-color: rgb(60, 60, 60);
        position: absolute;
        z-index: -1;
        transform: translateY(-25px);
        padding-top: 30px;
        max-height: 350px;
        overflow-y: auto;
        overflow-x: hidden;
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
    }

    .auto-suggestion::-webkit-scrollbar {
        background-color: rgb(32, 32, 32);
        width: 15px;
    }

    .auto-suggestion::-webkit-scrollbar-thumb {
        background-color:rgb(148, 148, 148);
        border-radius: 12px;
    }


</style>